import { format } from 'date-fns/fp'

import { FJsonAddressFragment } from '../graph/generated'

export const formatTime = format('H:mm')

export const formatAddress = (address: FJsonAddressFragment) => {
  if (!address.street) {
    if (address.city) {
      return `${address.city} ${address.houseNumber}`
    }
    return `${address.state}`
  }
  if (!address.houseNumber) {
    return `${address.street}, ${address.city}`
  }
  return `${address.street} ${address.houseNumber}, ${address.city}`
}
